<!-- 当前委托 -->
<template>
  <div class="recordTabel-wrapper">
    <el-table v-loading="loading" :data="tabelData" style="width: 100%" height="100%">
      <!-- 编号 -->
      <el-table-column prop="en_no" :label="$t('contract.order[0]')" width="140" align="center">
      </el-table-column>
      <!-- 名称 -->
      <el-table-column prop="pname" :label="$t('contract.order[1]')" width="100" align="center">
      </el-table-column>
      <!-- 方向 -->
      <el-table-column prop="otype" :label="$t('contract.order[2]')" width="100" align="center">
        <template slot-scope="scope">
            <div :class="scope.row.otype ==1?'green':'red'">{{scope.row.otype ==1?$t('contract.order[3]'):$t('contract.order[4]')}}</div>
        </template>
      </el-table-column>
      <!-- 委托量 -->
      <el-table-column prop="buynum" :label="$t('contract.order[24]')" width="120" align="center">
      </el-table-column>
      <!-- 委托价 -->
      <el-table-column prop="buyprice" :label="$t('contract.order[25]')" width="120" align="center">
      </el-table-column>
      <!-- 保证金 -->
      <el-table-column prop="totalprice" :label="$t('contract.order[9]')" width="120" align="center">
      </el-table-column>
      <!-- 手续费 -->
      <el-table-column prop="sxfee" :label="$t('contract.order[10]')" width="120" align="center">
      </el-table-column>
      <!-- 杠杆倍数 -->
      <el-table-column prop="leverage" :label="$t('contract.trader[19]')" width="100" align="center">
      </el-table-column>
      <!-- 委托时间 -->
      <el-table-column prop="addtime" :label="$t('contract.order[26]')" width="160" align="center">
          <template slot-scope="scope">
              <div>
                {{$public.timestampToTime(scope.row.addtime)}}
              </div>
            </template> 
      </el-table-column>
      <el-table-column width="100" :label="$t('contract.order[15]')">
        <template slot-scope="scope">
            <!-- 撤单 -->
          <div class="btn-cancel btn" @click="cancelOrder(scope.row.en_id)">{{$t('contract.order[27]')}}</div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import { weituo, cancel } from "@/api/contract"
  export default {
    props:{
      loop:Number
    },
    data() {
      return {
        tabelData: [],
        timer:"",
        loading:true,
        looptime: this.loop? this.loop :3000

      };
    },

    methods: {
      // 获取持仓表格数据
      getTabelData() {
        var sendData = {
          mobile: sessionStorage.mobile,
          p: this.page
        }
        weituo(sendData).then((r) => {
          this.loading = false
          if (r.data.status == 200) {
            // 需要处理的字段 去除无效零
            var arr = ['buyprice', 'buynum', 'totalprice', 'sxfee', 'newprice']
            var data = r.data.data.res.map((item, index) => {
              for (var i in item) {
                if (arr.includes(i)) {
                  item[i] = parseFloat(Number(item[i]))
                }
              }
              return item
            })
            this.tabelData = data
          }else{
            this.tabelData = []
          }
        }).catch((r) => {
          console.log(r);
          this.loading = false
        })
      },
      // 轮训
      loopAjax() {
        this.timer = setInterval(() => {
          this.getTabelData()
        }, this.looptime)
      },
      cancelOrder(id) {
        // 确认取消订单吗
        this.$confirm(this.$t('contract.tips[8]'), this.$t('contract.tips[9]'), {
          type: 'warning'
        }).then(() => {
          var sendData = {
            mobile: sessionStorage.mobile,
            order_id: id
          }
          cancel(sendData).then((r)=>{
            if(r.data.status == 200){
              this.$public.msg(this.$t('contract.tips[10]'), 'success', this)
              this.getTabelData()
              // 撤单成功 更新余额 表格
              this.$bus.$emit('order_success')
            }
          }).catch((r)=>{})
        }).catch(() => {
          // 取消弹框
        })
      }
    },
    created() {
      if (!sessionStorage.token) {
        this.loading = false
        this.tabelData = []
        return
      } else {
        // 获取表格数据
        this.getTabelData()
        // 轮训
        this.$nextTick(this.loopAjax)
      }
    },
    beforeDestroy() {
      window.clearInterval(this.timer)
    }


  }

</script>
<style lang=less>
  @import "../../assets/index.less";
  @import "./recordTabel.less";
</style>


