<!-- 历史成交-->
<template>
  <div class="recordTabel-wrapper">
    <el-table v-loading="loading" :data="tabelData" style="width: 100%" height="100%">
      <!-- 名称 -->
      <el-table-column prop="pname" :label="$t('contract.order[1]')" width="100" align="center">
      </el-table-column>
      <!-- 方向 -->
      <el-table-column prop="otype" :label="$t('contract.order[2]')" width="100" align="center">
        <template slot-scope="scope">
          <div :class="scope.row.otype ==1?'green':'red'">
            {{scope.row.otype ==1?$t('contract.order[3]'):$t('contract.order[4]')}}</div>
        </template>
      </el-table-column>
      <!-- 成交量 -->
      <el-table-column prop="buynum" :label="$t('contract.order[28]')" width="120" align="center">
      </el-table-column>
      <!-- 持仓价 -->
      <el-table-column prop="buyprice" :label="$t('contract.order[6]')" width="120" align="center">
      </el-table-column>
      <!-- 平仓价 -->
      <el-table-column prop="sellprice" :label="$t('contract.order[29]')" width="120" align="center">
      </el-table-column>
      <!-- 保证金 -->
      <el-table-column prop="totalprice" :label="$t('contract.order[9]')" width="120" align="center">
      </el-table-column>
      <!-- 手续费 -->
      <el-table-column prop="sxfee" :label="$t('contract.order[10]')" width="120" align="center">
      </el-table-column>
      <!-- 止盈价 -->
      <el-table-column prop="poit_win" :label="$t('contract.order[22]')" width="120" align="center">
      </el-table-column>
      <!-- 止损价 -->
      <el-table-column prop="poit_loss" :label="$t('contract.order[23]')" width="120" align="center">
      </el-table-column>
      <!-- 盈亏 -->
      <el-table-column prop="profit" :label="$t('contract.order[30]')" width="120" align="center">
      </el-table-column>
      <!-- 过夜费 -->
      <el-table-column prop="dayfee" :label="$t('contract.order[13]')" width="120" align="center">
      </el-table-column>
      <!-- 类型 -->
      <el-table-column prop="pc_type" :label="$t('contract.trader[15]')" width="120" align="center">
        <template slot-scope="scope">
          <div>
            {{transPCType(scope.row.pc_type)}}
          </div>
        </template>
      </el-table-column>
      <!-- 建仓时间 -->
      <el-table-column prop="addtime" :label="$t('contract.order[14]')" width="160" align="center">
        <template slot-scope="scope">
          <div>
            {{$public.timestampToTime(scope.row.addtime)}}
          </div>
        </template>
      </el-table-column>
      <!-- 平仓时间 -->
      <el-table-column prop="selltime" :label="$t('contract.order[31]')" width="160" align="center">
        <template slot-scope="scope">
          <div>
            {{$public.timestampToTime(scope.row.selltime)}}
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import { chengjiao } from "@/api/contract"
  export default {
    props: {
      loop: Number
    },
    data() {
      return {
        tabelData: [],
        loading: true,
        timer: "",
        looptime: this.loop ? this.loop : 3000,
        page: {
          p: 1,
          size: 10
        }
      };
    },
    computed: {

    },
    methods: {
      // 平仓类型 1手动平仓 2止盈平仓 3止损平仓 4系统强平
      transPCType(type) {
        var obj = {
          '1': this.$t("contract.state[0]"),
          '2': this.$t("contract.state[1]"),
          '3': this.$t("contract.state[2]"),
          '4': this.$t("contract.state[3]"),
        }
        return obj[type]
      },
      // 获取持仓表格数据
      getTabelData() {
        var sendData = {
          mobile: sessionStorage.mobile,
          p: this.page.p,
          size: 10
        }
        chengjiao(sendData).then((r) => {
          this.loading = false
          if (r.data.status == 200) {
            // 需要处理的字段 去除无效零
            var arr = ['buyprice', 'sellprice', 'poit_win', 'poit_loss', 'buynum', 'totalprice', 'sxfee', 'newprice']
            var data = r.data.data.res.map((item, index) => {
              for (var i in item) {
                if (arr.includes(i)) {
                  item[i] = parseFloat(Number(item[i]))
                }
              }
              return item
            })
            this.tabelData = data
          }
        }).catch((r) => {
          this.loading = false
        })
      },
      // 轮训
      loopAjax() {
        this.timer = setInterval(() => {
          this.getTabelData()
        }, this.looptime)
      },
    },
    created() {
      if (!sessionStorage.token) {
        this.loading = false
        return
      } else {
        // 获取表格数据
        this.getTabelData()
        // 轮训
        this.$nextTick(this.loopAjax)
        // 翻页
      }
    },
    beforeDestroy() {
      window.clearInterval(this.timer)
    }
  }
</script>
<style lang=less>
  @import "../../assets/index.less";
  @import "./recordTabel.less";
</style>


