<!--  -->
<template>
    <div class="contractOrder-page-wrapper">
        <div class="contractOrder-main">
                <!-- 我的订单 -->
            <h1>{{$t('contract.record[5]')}}</h1>
            <div class="info">
                    <!-- 浮动盈亏 -->
                <div class="item">{{$t('contract.record[3]')}}：{{$public.toIntercept(data.yingkui,4) }}</div>
                <!-- 可用资金： -->
                <div class="item">{{$t('contract.record[6]')}}：{{$public.toIntercept(data.keyong_price,4) }}</div>
                <!-- 冻结保证金： -->
                <div class="item">{{$t('contract.record[7]')}}：{{$public.toIntercept(data.totaldeposit, 4)}}</div>
                <div class="item green rate">{{$t('contract.record[4]')}}：{{data.risk}}</div>
            </div>
            <div class="radio-wrap">
                <div class="radio" @click="activeName='持仓'" :class="activeName =='持仓'?'active':''">{{$t('contract.record[0]')}}</div>
                <div class="radio" @click="activeName='委托'" :class="activeName =='委托'?'active':''">{{$t('contract.record[1]')}}</div>
                <div class="radio" @click="activeName='历史'" :class="activeName =='历史'?'active':''">{{$t('contract.record[2]')}}</div>
                <div class="laoyoutiao" :style="`left:${disco}`"></div>
            </div>
            <div class="tabel-wrap">
                <PositionTabel :page="page.p" :loop="looptime" v-if="activeName == '持仓'"></PositionTabel>
                <EnTrustTabel :page="page.p" :loop="looptime" v-if="activeName == '委托'"></EnTrustTabel>
                <HistroyTabel :page="page.p" :loop="looptime" v-if="activeName == '历史'"></HistroyTabel>
            </div>
            <!-- <pagination :page=page @change="changePage"></pagination> -->
        </div>
    </div>
</template>

<script>
    import { tongji } from '@/api/contract'
  import  pagination from "@/components/pagination";
    import EnTrustTabel from '@/components/contractRecord/enTrustTabel.vue'
    import PositionTabel from '@/components/contractRecord/positionTabel.vue'
    import HistroyTabel from '@/components/contractRecord/histroyTabel.vue'
    export default {
        data() {
            return {
                activeName: '持仓',
                looptime: 3000,
                page:{
                    p:1,
                    total:10,
                    size:10
                },
                data:{
                    yingkui:"--",
                    keyong_price:"--",
                    totaldeposit:"--",
                    risk:"--"
                }
            };
        },

        components: {
            PositionTabel,
            HistroyTabel,
            EnTrustTabel,
            pagination
        },

        computed: {
            disco() {
                var obj = {
                    '持仓': '0',
                    '委托': '131px',
                    '历史': '262px'
                }
                return obj[this.activeName]
            }
        },

        methods: {
            changePage(page) {
                this.page.p = page
            },
            // 统计
            getTongji() {
                    tongji({ account: JSON.parse(sessionStorage.account) }).then((r) => {
                        if (r.data.status == 200) {
                            this.data = r.data.data
                        }
                    })
            },
            // ajax伦旭
            loopAjax() {
                this.timer = setInterval(this.getTongji ,this.looptime)
            }
            
        },
        beforeDestroy() {
            window.clearInterval(this.timer)
            this.$bus.$off('upadte_pagination')
        },
        created() {
            this.getTongji()
            this.$nextTick(this.loopAjax)
            this.$bus.$on('upadte_pagination', (val)=>{this.page.total = val})
        },
        mounted() {
            if(!sessionStorage.token){
                this.$public.msg('请登录', 'warning', this)
                this.$router.push({path:'/login'})
            }
        }
    }

</script>
<style lang=less>
    @import "../../assets/index.less";
    @import "./contractOrder.less";
</style>


