<!-- 当前持仓-->
<template>
  <div class="recordTabel-wrapper">
    <el-table v-loading="loading" :data="tabelData" height="100%">
      <!-- 编号 -->
      <el-table-column prop="hold_no" :label="$t('contract.order[0]')" width="140" align="center">
      </el-table-column>
      <!-- 名称 -->
      <el-table-column prop="pname" :label="$t('contract.order[1]')" width="100" align="center">
      </el-table-column>
      <!-- 方向 -->
      <el-table-column prop="otype" :label="$t('contract.order[2]')" width="60" align="center">
        <template slot-scope="scope">
          <div :class="scope.row.otype ==1?'green':'red'">{{scope.row.otype ==1?$t('contract.order[3]'):$t('contract.order[4]')}}</div>
        </template>
      </el-table-column>
      <!-- 数量 -->
      <el-table-column prop="buynum" :label="$t('contract.order[5]')" width="120" align="center">
      </el-table-column>
      <!-- 持仓价 -->
      <el-table-column prop="buyprice" :label="$t('contract.order[6]')" width="120" align="center">
      </el-table-column>
      <!-- 最新价 -->
      <el-table-column prop="newprice" :label="$t('contract.order[7]')" width="120" align="center">
      </el-table-column>
      <!-- 盈亏 -->
      <el-table-column prop="fdyk" :label="$t('contract.order[8]')" width="120" align="center">
      </el-table-column>
      <!-- 保证金 -->
      <el-table-column prop="totalprice" :label="$t('contract.order[9]')" width="120" align="center">
      </el-table-column>
      <!-- 手续费 -->
      <el-table-column prop="sxfee" :label="$t('contract.order[10]')" width="120" align="center">
      </el-table-column>
      <!-- 止盈价 -->
      <el-table-column prop="poit_win" :label="$t('contract.order[11]')" width="120" align="center">
      </el-table-column>
      <!-- 止损价 -->
      <el-table-column prop="poit_loss" :label="$t('contract.order[12]')" width="120" align="center">
      </el-table-column>
      <!-- 过夜费 -->
      <el-table-column prop="dayfee" :label="$t('contract.order[13]')" width="120" align="center">
      </el-table-column>
      <!-- 建仓时间 -->
      <el-table-column prop="addtime" :label="$t('contract.order[14]')" width="160" align="center">
          <template slot-scope="scope">
              <div>
                {{$public.timestampToTime(scope.row.addtime)}}
              </div>
            </template> 
      </el-table-column>
      <el-table-column :label="$t('contract.order[15]')" width="180" align="center">
        <template slot-scope="scope">
            <!-- 平仓 -->
          <div class="btn-cancel btn" @click="overOrder(scope.row)">{{$t('contract.order[16]')}}</div>
          <!-- 设置 -->
          <div class="btn-cancel btn" @click="setOrder(scope)">{{$t('contract.order[17]')}}</div>
        </template>
      </el-table-column>
    </el-table>
    <!-- 平仓对话框 -->
    <el-dialog custom-class='reset-dialog' :visible.sync="dialog_over" width="332px">
        <!-- 平仓确认 -->
      <h1 slot="title">{{$t('contract.order[18]')}}</h1>
      <el-input v-model.number="num">
          <!-- 平仓张数 -->
        <div class='slot-prefix' slot='prefix'>{{$t('contract.order[19]')}}:</div>
      </el-input>
      <span slot="footer" class="dialog-footer">
          <!-- 取 消 -->
        <el-button class="btn-close btn" @click="dialog_over = false">{{$t('contract.order[20]')}}</el-button>
        <!-- 确 定 -->
        <el-button class="btn-confirm btn" type="primary" :loading="overLoading" @click="confirmOver">{{$t('contract.order[21]')}}</el-button>
      </span>
    </el-dialog>
    <!-- 设置对话框 -->
    <el-dialog custom-class='reset-dialog' :visible.sync="dialog_set" width="332px">
        <!-- 设置 -->
      <h1 slot="title">{{$t('contract.order[17]')}}</h1>
      <div class="set-line">
        <el-input v-model="zy">
            <!-- 止盈价格 -->
          <div class='slot-prefix' slot='prefix'>{{$t('contract.order[22]')}}:</div>
        </el-input>
        <div class="tips" v-if="scopeData.otype==1">≥ {{zyRange}}</div>
        <div class="tips" v-else>≤ {{zyRange}}</div>
      </div>
      <div class="set-line">
        <el-input v-model="zs">
            <!-- 止损价格 -->
          <div class='slot-prefix' slot='prefix'>{{$t('contract.order[23]')}}:</div>
        </el-input>
        <div class="tips" v-if="scopeData.otype==1">≤ {{zsRange}}</div>
        <div class="tips" v-else>≥ {{zsRange}}</div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button class="btn-close btn" @click="dialog_set = false">{{$t('contract.order[20]')}}</el-button>
        <el-button class="btn-confirm btn" :loading="setLoading" type="primary" @click="confirmSet">{{$t('contract.order[21]')}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { chicang, pingcang, set_poit_pre, set_poit } from "@/api/contract"
  export default {
    props:{
      loop:Number
    },
    data() {
      return {
        // 各种loading
        loading: true,
        overLoading: false,
        setLoading: false,
        // 轮询定时器实例
        timer: "",
        order_id: "",
        page: "1",
        // 平仓数量
        num: "",
        // 最大平仓数量
        maxNum:"",
        // 当前点击行的数据
        scopeData: "",
        zy: "",
        zs: "",
        // 当前币种所需设置参数
        setInfo: "",
        dialog_over: false,
        dialog_set: false,
        tabelData: [],
        curIndex:'',
        looptime: this.loop ? this.loop :3000

      };
    },
    watch: {
      'num'(n, o){
        if(isNaN(Number(n))){
          this.num = o
        }
        if(Number(n) > Number(this.maxNum)){
          this.num = this.maxNum
        }
        
      },
      // 清空数据
      'dialog_over'() {
        this.num = ""
      },
      'dialog_set'() {
        this.zy = ""
        this.zs = ""
      },
      'zy'(n, o) {
        var de = String(n).split(".")
        if (isNaN(Number(n))) {
          this.zy = o
        }
        // 位数限制 
        if (de.length == 2) {
          if (de[1].length > this.$public.savePoint(this.scopeData.code)) {
            this.zy = o
          }
        }

      },
      'zs'(n, o) {
        var de = String(n).split(".")
        if (isNaN(Number(n))) {
          this.zs = o
        }
        // 位数限制 
        if (de.length == 2) {
          if (de[1].length > this.$public.savePoint(this.scopeData.code)) {
            this.zs = o
          }
        }
      }

    },
    computed: {
      //  止损范围
      zsRange() {
        var zsprice;
        // 买涨 止损价格<= 最新价格-最小止损价
        if (this.scopeData.otype == 1) {
          zsprice = Number(this.scopeData.newprice) - Number(this.setInfo.min_zs)
          if (zsprice < 0) {
            return 0
          } else {
            return this.$public.transPrice(zsprice, this.scopeData.code)
          }

        } else {
          // 买跌 止损价格>= 最新价格+最小止盈价
          zsprice = Number(this.scopeData.newprice) + Number(this.setInfo.min_zs)
          return this.$public.transPrice(zsprice, this.scopeData.code)
        }

      },
      // 止盈范围
      zyRange() {
        var zyprice;
        // 买涨 止盈价格>= 最新价格+最小止盈价
        if (this.scopeData.otype == 1) {
          zyprice = Number(this.scopeData.newprice) + Number(this.setInfo.min_zy)
          return this.$public.transPrice(zyprice, this.scopeData.code)

        } else {
          // 买die 止盈价格<= 最新价格-最小止盈价
          if (Number(this.scopeData.newprice) - Number(this.setInfo.min_zy) < 0) {
            zyprice = 0
            return zyprice
          } else {
            zyprice = Number(this.scopeData.newprice) - Number(this.setInfo.min_zy)
            return this.$public.transPrice(zyprice, this.scopeData.code)
          }
        }
      }
    },

    methods: {
      // 获取持仓表格数据
      getTabelData() {
        var sendData = {
          mobile: sessionStorage.mobile,
          p: this.page
        }
        chicang(sendData).then((r) => {
          this.loading = false
          if (r.data.status == 200) {
            // 需要处理的字段 去除无效零
            var arr = ['buyprice', 'buynum', 'totalprice', 'sxfee', 'dayfee', 'poit_win', 'poit_loss', 'fdyk', 'newprice']
            var data = r.data.data.res.map((item, index) => {
              for (var i in item) {
                if (arr.includes(i)) {
                  item[i] = parseFloat(Number(item[i]))
                }
              }
              return item
            })
            this.tabelData = data
            if(this.curIndex) {
              this.scopeData = data[this.curIndex]
            }
          } else {
            this.tabelData = []
          }
        }).catch((r) => {
          this.loading = false
        })
      },
      // 轮询
      loopAjax() {
        this.timer = setInterval(() => {
          this.getTabelData()
        }, this.looptime)
      },
      // 设置显示
      setOrder(scope) {
        this.scopeData = scope.row
        this.curIndex = scope.$index
        // 获取设置所需参数
        set_poit_pre({ pid: this.scopeData.pid }).then((r) => {
          if (r.data.status == 200) {
            this.setInfo = r.data.data
            // 获取成功打开对话框
            this.dialog_set = true
          } else {
            this.$public.msg(r.data.msg, 'error', this)
          }
        })
      },
      // 确认设置持仓
      confirmSet() {
        // 什么都没输入关闭弹窗
        if(!this.zy && !this.zs){
            this.dialog_set = false
            return
        }
        var sendData = {
          h_id: this.scopeData.hold_id,
        }
        this.zy? sendData.zy= this.zy : ''
        this.zs? sendData.zs= this.zs : ''
        this.setLoading = true
        set_poit(sendData).then((r) => {
          this.setLoading = false
          if (r.data.status == 200) {
            // 设置成功！
            this.$public.msg(this.$t('contract.tips[5]'), 'success', this)
            this.dialog_set = false
            // 设置成功 更新表格
            this.getTabelData()
          }else{
            this.$public.msg(r.data.msg, 'error', this)
            this.dialog_set = false
          }
        })

      },

      // 平仓显示
      overOrder(data) {
        this.maxNum = data.buynum
        this.dialog_over = true
        this.order_id = data.hold_id
      },
      // 确认平仓
      confirmOver(id) {
        // 平仓数量不能为空
        if (this.num == "") {
          this.$public.msg(this.$t("contract.tips[6]"), 'warning', this)
          return
        }
        this.overLoading = true
        var sendData = {
          num: this.num,
          order_id: this.order_id
        }
        pingcang(sendData).then((r) => {
          this.overLoading = false
          if (r.data.status == 200) {
            this.dialog_over = false
            this.$public.msg(this.$t("contract.tips[7]"), 'success', this)
            // 更新余额 表格
            this.$bus.$emit('order_success')
          } else {
            this.dialog_over = false
            this.$public.msg(r.data.msg, 'error', this)
          }
        }).catch((r) => {
          this.overLoading = false
        })

      },
    },
    created() {
      if (!sessionStorage.token) {
        this.loading = false
        this.tabelData = []
        return
      } else {
        // 获取表格数据
        this.getTabelData()
        // 轮训
        this.$nextTick(this.loopAjax)
      }
    },
    beforeDestroy() {
      window.clearInterval(this.timer)
    }


  }

</script>
<style lang=less>
  @import "../../assets/index.less";
  @import "./recordTabel.less";
</style>


